.watch {
  width: 100vw;
  height: 100vh;

  .back {
    display: flex;
    align-items: center;
    position: absolute;
    top: 10px;
    left: 10px;
    color: white;
    cursor: pointer;
    z-index: 2;
    background-color: rgb(22, 22, 22, 0.5);
    padding: 1rem;
   
  }


  .video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
